  import {jsonp} from 'common/js/jsonp'
 import {commonParams,ops} from 'api/config'

 export function getHotKey(){
let url="https://c.y.qq.com/splcloud/fcgi-bin/gethotkey.fcg",
data=Object.assign({},commonParams,{uin:0,plateform:'h5',needNewCode:1})

return jsonp(url,data,ops);
 }
 export function getSeachList(w,p){

let url='https://c.y.qq.com/soso/fcgi-bin/search_for_qq_cp',
data=Object.assign({},commonParams,{format:'jsonp',uin:0,plateform:'h5',needNewCode:1,zhidaqu:1,
catZhida: 1,flag: 1,perpage: 20,w,t: 0,ie: 'utf-8',sem: 1,
aggr: 0,n: 20,p,remoteplace: 'txt.mqq.all'})

return jsonp(url,data,ops)
 }











