<template>

	<ul class="songList" ref="list">
		<li class="singer-zhida song-list-item" v-if="musicList.zhida.singermid" @click="set(musicList.zhida.singermid)">
			<img v-if="musicList.zhida.singermid" v-lazy="imgSrc" >
			<h5>{{musicList.zhida.singername}}</h5>
			<p>
				<span>单曲: {{musicList.zhida.songnum}}</span>
				<span>专辑: {{musicList.zhida.albumnum}}</span>
			</p>

		</li>	
		<!-- <li class="singer-zhida song-list-item" v-else-if="musicList.zhida.albumname" @click="set(musicList.zhida.singermid)">
			<img v-if="musicList.zhida.albummid" :src="musicList.zhida.albummid|photoSrc" >
			<h5>{{musicList.zhida.albumname}}</h5>
			<p>
				{{musicList.zhida.singername}}
			</p>
			
		</li> -->


		<li v-for="(item,index) in musicList.list" class="song-list-item" :key="index" @click="song(item,index)">
			<i class="icon-bofang"></i>
			<h5 v-html="item.songname"></h5>
			<p v-html="item.singer"></p>
			
		</li>
		<keep-alive>
			<li class="song-list-item loading" v-if="!musicList.isAll">
				<i class="icon-jiazai"></i>
			</li>
			<li class="loaded song-list-item" v-else>已全部加载</li>
		</keep-alive>
	</ul>
</template>

<script>
import {mapMutations,mapActions} from "vuex"
import {playMode} from "common/js/config"
import {getVkey} from "api/song"

export default{ 
	name:"music-list",
	methods:{
		song(item){
getVkey(item.songmid).then(res=>this.setVkey(res.data.items[0].vkey))
			this.selectPlay({list:[item],index:0,fullScreen:true});
			this.setMode(playMode.loop)
		
		},
		set(item){
			this.$router.push({path:`/set/${item}`})
			this.setSet(item)
		},
		...mapMutations({			
			"setSet":"SET_SET",
			"setMode":"SET_MODE",
			"setVkey":"SET_VKEY"

		}),
		...mapActions([
			"selectPlay",
			"randomPlay"
			])
		

	},
	filters:{
		musicSrc(val){		

			return 
		},
		photoSrc(val){return "https://y.gtimg.cn/music/photo_new/T002R68x68M000"+val+".jpg?max_age=2592000"}

	},
	computed:{
		imgSrc(){
			return "https://y.gtimg.cn/music/photo_new/T001R68x68M000"+this.musicList.zhida.singermid+".jpg?max_age=2592000"
		}
	},
	props:{

		musicList:{
			type:Object,
			default(){
				return{
					isAll:false,
					zhida:{
						albumid:"",
						albumnum:"",
						albummid:"",
						albumname:"",
						singername:"",
						singermid:"",
						songnum:"",
						type:0
					},
					list:[{songname:"",songmid:"",singer:"",songid:"",albummid:""},]
				}


			}
		}},
		data(){return {}}

	}

	</script>

	<style lang="less" type="text/less" scoped>
	@import "~common/less/variable";
	@import "~common/less/mixin";

	@keyframes ro{
		from{
			transform: rotateZ(0);

		}
		to{
			transform: rotateZ(360deg);

		}
	}
	.songList{
		background-color: @color-background-l;
		.song-list-item{
			position: relative;
			padding-left: 110px; 
			height: 119px;
			overflow: hidden;
			border-top: 1px solid @border-color-d;
			.icon-bofang{
				position: absolute;
				font-size: @font-size-large-x;top:36px;left:36px;
			}
			h5{
				margin:20px 0 10px;
				line-height: 32px;
				font-size: @font-size-medium-x;
				color:@color-text;
				.text-over-flow;
			}
			p{
				color:#808080;
				.text-over-flow;
				font-size: @font-size-small;
			}
		}
		.singer-zhida{
			img{
				position:absolute;
				left: 8px;
				top: 8px;
				width: 80px;
				height: 80px;
			}

		}
		.loading{
			text-align: center;
			padding:0;

			.icon-jiazai{
				font-size: 80px;
				line-height: 120px;
				margin:20px auto;
				position: absolute;
				left: 50%;
				margin-left:-40px;
				animation: ro .5s linear infinite both;

			}
		}
		.loaded{
			text-align: center;
			line-height: 110px;
			padding:0;
		}
	}
	</style>


