<template>
  <scroll>
    <loading v-if="loading"></loading>
<div  v-else >
   <slider :items="sliders" class="slider"></slider>
    <radio :items="songListItems" :title="songListTitle"></radio>
<hot-diss :items="dissList" :title="dissTitle"></hot-diss>
    
</div>
   
  </scroll>
  </template>

<script>
import Scroll from "components/base/Scroll"
import Loading from "components/base/Loading"
import {getRecommend} from 'api/recommend'
import {ERR_OK} from 'api/config.js'
import Slider from 'components/base/Slider'
import Radio from 'components/recommend/Radio'
import HotDiss from 'components/recommend/HotDiss'
import {mapMutations} from "vuex"
import jsonp from 'jsonp'

export default {
  name: 'recommend',
  components:{
    Scroll,
  	Slider,
    Loading,
    HotDiss,
    Radio
  },
  computed:{
...mapMutations({
})
  },
  data(){
  	return{ 
       sliders:[],
       songListTitle:"电台",
       songListItems:[],
       loading:true,
       dissTitle:"热门歌单",
       dissList:[]
  	}
  },
  created(){
    this._getRecommend();


  },
  methods:{
  	_getRecommend(){
  		getRecommend().then(res=>{

  			if(res.code===ERR_OK){ 

  				this.sliders=res.data.slider;
          this.songListItems=res.data.radioList;
          this.loading=false;
          this.dissList=res.data.songList;
         
  			}
  		})
  	}
  }
}
</script>

<style lang="less" type="text/less" scoped>
.slider{
  margin-bottom:14px;
}
</style>



