<template>
  <div class="panel">
    <h3 class="panel-title">{{title}}</h3>
    <ul class="panel-list">
      <li class="panel-list-item" v-for="item in items" @click="getDissList(item)">
        <div class="diss-pic">
         <img v-lazy="item.picUrl" class="list-item-bg"> 
         <div class="list-item-accessnum">
          <i class="icon-erji"></i>
          <span class="accessnum">{{item.accessnum | round}}</span>
          <i class="list-item-play icon-play"></i>
        </div>

      </div>

      <p class="list-item-des">{{item.songListDesc}}</p>
      <h4 class="list-item-author">{{item.songListAuthor}}</h4>
    </li>
  </ul>
</div>
</template>
<script>
import {mapMutations} from "vuex"


// import axios from "axios"
export default {
  name: "list",
  filters:{
    round(val){
      return Math.round(val/1000)/10+"万"
    }
  },
  methods:{
    getDissList(item){
     this.setSet(item.id);
     this.$router.push({path:`/diss/${item.id}`})
    },
...mapMutations({
  setSet:"SET_SET"
}),

},


props: {
  title: {
    type: String,
    default: ""
  },
  items: {
    type: Array,
    default () {
      return [{
        accessnum:"",
        pic_mid:"",
        picUrl:"",
        songListAuthor:"",
        songListDesc:""
      }]
    }

  }
},
data(){
  return{
    list:[]
  }
}

}

</script>
<style lang="less" type="text/less" scoped>
@import "~common/less/index"; 
.panel{
 margin: 0 20px 14px; 

 .panel-title{ font-size: font-size-medium-x; margin-bottom:22px; } 
 .panel-list{ display: flex; flex-flow: row wrap; 
  justify-content: space-between; 
  .panel-list-item{ 
    background-color: @color-background-l;
    width:48%; 
    margin-bottom: 20px;
    position: relative; 
    .diss-pic{
      position: relative;
      overflow:hidden;
      .list-item-bg{ 
        display: block; 
        width: 100%; 
        margin-bottom:10px;
      } 
      .list-item-accessnum{ 
        position: absolute; 
        left: 0; 
        bottom:20px; 
        color:@color-text-l; 
        width: 100%;

        font-size: @font-size-small;
.accessnum{
  padding-left: 50px;
}
        .icon-erji{ 
          font-size: @font-size-medium-x;
          padding-right:10px;
          position: absolute;
          left: 10px;
          bottom: 0;
        }
        .list-item-play{ 
          position: absolute; 
          right: 10px; 
          font-size: @font-size-large-s; 
          bottom:5px; 
          color:@color-text-l; 
        } 
      }
    }



    .list-item-des{ 
      text-overflow: ellipsis; 
      white-space: nowrap; 
      overflow: hidden; 
      font-size: @font-size-medium; 
      padding: 0px 14px 10px;
    } 
    .list-item-author{ .list-item-des; font-size: @color-text-l; padding-top:0;} 
  } 
} 
}
</style>



