<template>
    <dl class="hot-search">
    	<dt class="title">热门搜索</dt>
    	<dd class="special-key hot-keys"><a :href="special.url">{{special.key}}</a></dd>

	<dd v-for="hot in hotkey" :key="hot.n" class="hot-keys" @click="copy(hot.k)">
		{{hot.k}}
	</dd>
    </dl>
</template>

<script>
	export default{ 
		name:"hot-search",
        methods:{
            copy(val){
    
                this.$emit("copyHot",val)
            }
        },
		props:{
            // copyHot{
            //     type:Function,
            //     default(){return()=>{}}
            // },
			hotkey:{
				type:Array,
				default(){
					return [{k:"",n:""}]
				}
			},
			special:{
				type:Object,
				default(){return {key:"",url:""}}
			}
		}

	}
    
</script>

<style lang="less" type="text/less" scoped>
@import '~common/less/variable';
    .hot-search{
    	padding: 30px;
        background-color: @color-background-l;
.title{
	color:rgba(0,0,0,.6);
	margin-bottom:16px;
    }
    .hot-keys{
    	display: inline-block;
    	font-size: @font-size-medium;
        color:@color-text;
        border:1px solid rgba(0,0,0,.6);
        margin-bottom:20px; 
        margin-right:28px;
        line-height: 60px;
        padding: 0 20px;
        border-radius: 200px;
        &.special-key{
        	
        	border-color:@color-danger;
        	a{
        		color:@color-danger;
        	}
        }
    }
}
</style>


