 function offsetTop(el){
 	let body=document.body||document.documentElement;
 	let offsetTop=el.offsetTop||0;

 	while(el&&el.offsetParent!=body){
 		// console.log(el.offsetParent)
 		offsetTop+=el.offsetTop;
 		el=el.offsetParent;
 		// console.log(offsetTop)
 	}
 	return offsetTop;
 }
 export function isBottom(el){


 	let h1=window.screen.height*2,
 	h2=document.documentElement.scrollTop||document.body.scrollTop;
 	// console.log(h1+h2,offsetTop(el)+el.clientHeight)
 	console.log(h1,h2,offsetTop(el),el.clientHeight)
return h1+h2 >=offsetTop(el)+el.clientHeight

 	
 	
 }
 export function stopScroll(height,val){

 	let el=document.body||document.documentElement;
				el.style.height=height;
				el.style.overflow=val;
 }
 export function fixed(el){
 	            el.width="100%";
				el.style.height=window.innerHeight+"px";
				let val=document.body.scrollTop||document.documentElement.scrollTop;
				el.style.top=`${val-185}px`;
				stopScroll("100%","hidden");

 }
 export function offsetTop(el){
 	if(el){
 		let offsetTop=el.offsetTop;
 		while(el.offsetParent.tagName.toLowerCase()!="body"){
 			
 			el=el.offsetParent;
 			offsetTop+=el.offsetTop;

 			

 		}
 		return offsetTop;
 	}
 	return 0;
 }


