<template>
<scroll>
 <loading v-if="loading"></loading>
  <ul class="rank" key="rank" v-else>
  <li class="top-list" v-for="item in topList" :key="item.id" @click="setTopId(item.id)">
    <img v-lazy="item.picUrl" class="top-list-img">
    <h4 class="top-list-title"> {{item.topTitle}}</h4>
    <p class="top-list-song" v-for="(song,index) in item.songList" :key="index">
      {{index+1}}
      <span>{{song.songname}}</span>
      -{{song.singername}}
    </p>
    
<i class="icon-qianjin"></i>
  </li>
</ul>
<router-view></router-view>
</scroll>

</template>
<script>
import {getRank} from 'api/rank'
import Loading from "components/base/Loading"
import {ERR_OK} from 'api/config'
import Scroll from "components/base/Scroll"
import {mapMutations} from "vuex"

export default {
  name: 'rank',
  components:{
    Scroll,
    Loading

  },
  data(){return{
    loading:true,
    topList:[
    {
      id:"",
      picUrl:"",
      topTitle:"",
      songList:[
         {
          singername:"",
          songname:""
         }
      ]
    }
    ]
  }},
  methods:{
  	_getRank(){ 
  		getRank().then((res)=>{
        if(res.code===ERR_OK){
          this.topList=res.data.topList;
          this.loading=false
        }
    
      })

  	},
    setTopId(id){
      this.$router.push({path:`/top/${id}`});

      this.setTop(id);
    },
    ...mapMutations({
      setTop:"SET_TOP"
    })
  },
  created(){
  	this._getRank();
  }
}
</script>

<style lang="less" type="text/less" scoped>
@import "~common/less/variable";
  .rank{
  margin:20px;

  .top-list{
      height: 200px;
      box-sizing: border-box;
  margin-bottom:20px; 
  position:relative;
  background-color: @color-background-l;
  padding: 15px 20px 0 230px;
  .top-list-img{
  position: absolute;
  left:0; 
  top:0;
  width: 200px;
  height: 200px;
}
.top-list-title{
font-size: @font-size-medium-x;
margin-bottom: 10px;
white-space: nowrap;
text-overflow: ellipsis;
overflow:hidden;
}
.top-list-song{
color:rgba(0,0,0,.5);
font-size: @font-size-medium;
text-overflow: ellipsis;
overflow: hidden;
white-space: nowrap;
line-height: 1.5;
span{
  color:@color-text; 
  padding: 0 10px 0 16px;
}
}
.icon-qianjin{
  position: absolute;
  right:20px;
  top:50%;font-size: @font-size-small-s;
}
}
}

</style>



