 function getSinger(item){
 	let ary=new Array;
 	item.forEach(item=>{
ary.push(item.name)
 	})
 	return ary.join(" / ")

 }
 class Music{
 	constructor({songname,songmid,singer,songid,albummid}){
 		this.songname=songname;
 		this.songmid=songmid;
 		this.singer=singer;
 		this.songid=songid;
 		this.albummid=albummid;

 	}
 }
 export function getMusicList(listData){
return new Music({
	songname:listData.songname,
	songmid:listData.songmid,
	singer:getSinger(listData.singer),
	songid:listData.songid,
	albummid:listData.albummid,
})
 }
 class Radio{
 	constructor({songname,songmid,singer,albummid,songid}){
 		this.songname=songname;
 		this.songmid=songmid;
 		this.singer=singer;
 		this.albummid=albummid;
 		this.songid=songid
 	}
 }
 export function getRadioList(listData){
 	return new Radio({
 		songname:listData.name,
 		songmid:listData.mid,
 		singer:getSinger(listData.singer),
 		albummid:listData.album.mid,
 		songid:listData.id

 	})
 }
  class Diss{
 	constructor({songname,songmid,singer,songid,albummid}){
 		this.songname=songname;
 		this.songmid=songmid;
 		this.singer=singer;
 		this.songid=songid;
 		this.albummid=albummid;

 	}
 }
  export function getDissList(listData){
return new Music({
	songname:listData.name,
	songmid:listData.mid,
	singer:getSinger(listData.singer),
	songid:listData.id,
	albummid:listData.album.id,
})}




