<template>
	<transition name="translate">
		<div class="music-detail" ref="music" >
			<img :src=imgSrc class="blur-filter">
			<div class="test-lyric-height" ref="testHeight">11111</div>
			<header class="music-detail-header">
				<div class="music-intr">
					<h4 v-html="currentSong.songname"></h4>
					<p v-html="currentSong.singer"></p>
				</div>
				<i class="icon-fanhui" @click="back"></i>
				
			</header>
			<main class="lyric" ref="main":class="{lyricActive:onLyric}">
				
				<div class="lyric-container" :class="{rotate:lyricMode}">
					<div class="albumn"  >
						<div class="film"  @click.stop="play" >
							<div class="film-img">
								<img :src="imgSrc"  class="albumn-img" ref="img" :class="{run:playing}">
							</div>
							<i :class="{iconPlay:!playing,iconPause:playing}" ></i>

						</div>
						
					</div>
					<div class="lyric-play" ref="lyricOut">
						<div class="lyric-play-wrap" >
							<p ref="lyricLines" class="lyric-text" :class="{active:index===activeLine}"v-for="(list,index) in lyricText">{{list.text}}</p>
						</div>
					</div>
				</div>
				<div class="lyric-control">
					<a href="javascript:;" @click="changelyricMode(false)" :class="{active:!lyricMode}"></a>
					<a href="javascript:;" :class="{active:lyricMode}" @click="changelyricMode(true)"></a>
				</div>
				

				

			</main>
			<footer class="music-control">
				<p class="colect">
					<i class="icon-liebiao" href="#sequenceList" @click="tab=!tab" :class="{active:tab}"></i>
					<ul class="sequenceList" :class="{showSequenceList:tab}">
						<li v-for="(item,index) in sequenceList" :class="{active:currentIndex===index}" @click.stop="selectMusic(item,index)">
							{{item.songname}}--{{item.singer}}
						</li>
					</ul>
				</p>
				<div class="music-time">
					<span class="music-start">
						{{currentTime}}
					</span>
					<div class="music-time-line" @click="changeTime" ref="processWrap">
						<div class="bg-line"></div>
						<div class="music-process" ref="process"></div>
					</div>
					<div class="music-end" >
						<span v-show="duration">{{duration}}</span>
					</div>
					
				</div>
				<i :class="playmode" class="selectMode" @click="changePlayMode"></i>
				<i class="icon-volume" @click="showVolumn=!showVolumn" :class="{active:showVolumn}"></i>

				<a :href="audioSrc" class="download" target="_blank">下载</a>
				<div class="volumn"  @click="clickVolumn" ref="volumnWrap" v-show="showVolumn">
					<span class="circle" ref="volumn"></span>
				</div>
			</footer>
			<div class="bg-blur">
			</div>
			<div class="bg-mask"></div>
		</div>
	</transition>
</template>

<script>
import {ERR_OK} from "api/config"
import {getLyric} from "api/song"
import {mapGetters,mapMutations,mapActions} from "vuex"
import {playMode} from "common/js/config"
import {formatLyric} from "common/js/lyric"
import {math} from "common/js/array"
import {offsetTop} from "common/js/dom"
import {getVkey} from "api/song"
export default{ 
	name:"player",
	beforeDestroyed(){

		this.removeEventListener();
		document.querySelector("audio").pause();
		this.setFullScreen(false);
	},
	created(){
		if(!this.currentSong.songmid){
			this.$router.back;
			return;
		}
		this._getLyric();
	},
	computed:{
		loop(){
			return this.mode===playMode.loop;
		},
		audio(){
			return document.querySelector("audio");
		},
		playmode(){
			if(this.mode==playMode.sequence){
				return "icon-liebiaobofang"
			}else if(this.mode===playMode.loop){
				return "icon-danquxunhuan";
			}
			return "icon-suijibofang"

		},
		audioSrc(){
			return this.audio.src;
		},
		imgSrc(){

			return `https://y.gtimg.cn/music/photo_new/T002R150x150M000${this.currentSong.albummid}.jpg?max_age=2592000"`;
		},
		currentSong(){
			return this.sequenceList[this.currentIndex]
		},
		...mapGetters([
			"currentIndex",
			"playing",
			"sequenceList",
			"mode",
			"volumn"
			])
	},
	watch:{
		currentSong(newv,oldv){
			this._getLyric();
		}

		
	},
	methods:{
		_getLyric(){
			getLyric(this.currentSong.songid).then(res=>{
				if(res.code===ERR_OK){
					this.lyricText=formatLyric(res.lyric);


				}
			})
		},
		selectActive(){
			let [,m,s]=/^(\d{2}):(\d{2})$/.exec(this.currentTime),
			lyricIndex=-1;
			this.lyricText.forEach((item,index)=>{
				if(item.minite==m&&item.second==s){
					lyricIndex=index;
				}
			})

			this.activeLine=lyricIndex>0?lyricIndex:this.activeLine;
			if(this.$refs.testHeight){
		this.lyricHeight=this.$refs.testHeight.offsetHeight;
				
			}

			console.log(this.lyricHeight)
			if(this.activeLine>5){
				let n=(this.activeLine-5)*this.lyricHeight;
				this.$refs.lyricOut.scrollTo(0,n,1000)

		}else{
			this.$refs.lyricOut.scrollTo(0,0,1000)
		}
		},
		changelyricMode(val){
			this.lyricMode=val;
		},
		clickVolumn(e){


			let percent=(1-(e.clientY-offsetTop(this.$refs.volumnWrap))/this.$refs.volumnWrap.offsetHeight).toFixed(2);
			this.setVolumn(percent);
			this.$refs.volumn.style.height=percent*this.$refs.volumnWrap.offsetHeight+"px";
			this.$refs.volumn.volumn=percent;
		},
		selectMusic(item,index){
			this.setCurrentIndex(index);
			getVkey(item.songmid).then(res=>this.setVkey(res.data.items[0].vkey))
			this.tab=!this.tab;
		},
		back(){
			this.setFullScreen(false);
			this.setPlaying(false);
			this.audio.pause();
		},
		addEventListener(){
			let el=this.audio;
			el.addEventListener("timeupdate",this.upData);
			el.addEventListener("canplay",this.getDuration);
		},
		removeEventListener(){
			let el=this.audio;
			el.removeEventListener("timeupdate",this.upData);
			el.removeEventListener("canplay",this.getDuration);
		},
		
		getDuration(){
			this.duration=this.getTime(this.audio.duration);
			
		},

		changePlayMode(){
			if(this.mode==playMode.sequence){
				this.setMode(playMode.loop);
				return;
			}else if(this.mode===playMode.loop){
				this.setMode(playMode.random);
				return;
			}
			this.setMode(playMode.sequence)
		},
		changeTime(e){
			let el=this.$refs.process;
			this.audio.currentTime=(e.clientX-this.offsetLeft(el))/this.$refs.processWrap.offsetWidth*this.audio.duration;
			this.upData();
			this.audio.play();
			this.setPlaying(true);

		},
		offsetLeft(e){
			let left=e.offsetLeft;

			while(e.offsetParent){
				left+=e.offsetParent.offsetLeft;
				e=e.offsetParent;
			}
			return left;
		},
		_inits(){


			this.currentTime=this.getTime(this.audio.currentTime);
			this.addEventListener();
		},
		getTime(t){
			let m=Math.floor(t/60),
			s=Math.floor(t%60);
			m=m<10?("0"+m):m;
			s=s<10?("0"+s):s;
			return m+":"+s;
		},
		upData(){	
			
			this.currentTime=this.getTime(this.audio.currentTime);
			this.percent=(this.audio.currentTime/this.audio.duration*100).toFixed(2);
			if(this.$refs.process){
				this.$refs.process.style.width=this.percent+"%";
			}
			this.selectActive();

		},
		play(e){
			e=window.event||e;
			e.stopPropagation();
			if(this.playing){
				this.audio.pause();

			}else{
				this.audio.play();
			}
			this.setPlaying(!this.playing);

		},
		
		...mapMutations({
			"setFullScreen":"SET_FULLSCREEN",
			"setPlaying":"SET_PLAYING",
			"setCurrentIndex":"SET_CURRENTINDEX",
			"setMode":"SET_MODE",
			"setVolumn":"SET_VOLUMN",
			"setVkey":"SET_VKEY",
		})
	},		
	mounted(){
		this._inits();
		this.$refs.volumn.style.height=200*this.volumn+"px";

	},
	data(){
		return{
			duration:"",
			currentTime:0,
			percent:"",
			rotateR:"",
			tab:false,
			showVolumn:false,
			onLyric:false,
			routateTime:"",
			rotateOn:true,
			lyricMode:false,
			lyricText:[],
			activeLine:0,
			lyricHeight:0,

		}
	},


}

</script>

<style lang="less" type="text/less" scoped>
@import "~common/less/variable";
@import "~common/less/mixin";

@keyframes rotate{
	from{
		transform: rotateZ(0);
	}
	to{
		transform: rotateZ(360deg);
	}
}
@keyframes down{
	from{
		transform: translateY(300px);
	}
	to{
		transform: translateY(0);
	}
}
@keyframes up{
	from{
		transform: translateY(-200px);
	}
	to{
		transform: translateY(0);
	}
}
.music-detail{
	position: absolute;
	top: 0;
	left: 0;
	bottom:0;
	right:0;
	max-width: 640px;
	overflow:hidden;
	z-index: 1001;
	color:@color-text-w;
	box-sizing: border-box;
	background-color: #000;
	display: flex;
	flex-flow: column nowrap;
	.blur-filter{
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		opacity: .98;
		z-index: -1;
		filter:blur(70px);
	}

	.music-detail-header{
		
		height: 160px;
		transform: translateY(-200px);
		background-color:rgba(0,0,0,.3);
		animation:up .5s .5s ease-in forwards;
		.music-intr{
			color:@color-text-l;
			font-size: @font-size-medium-x;
			white-space: nowrap;
			
			height: 200px;
			line-height: 2;

			
			h4{
				padding-top:10px;
				text-align: center;
				line-height:2;

				font-size: 40px;
			}
			p{
				text-align: center;
				text-align-last: center;
				font-size:@font-size-medium ;
			}

		}
		.icon-fanhui{
			color:@color-text-l;
			position: absolute;
			left: 20px;
			top: 0;
			line-height: 130px;
			font-size: 60px;
		}


	}
	.lyric{
		flex:1;
		position:relative;
		box-sizing: border-box;
		transition: all .5s linear;
		transform-style: preserve-3d;
		.lyric-control{
			position: absolute;
			left: 50%;
			bottom:20px;
			margin-left:-35px;
			z-index: 9999;
			height: 30px;
			a{
				width: 20px;
				height: 20px;
				line-height: 60px;
				display:inline-block;
				border-radius: 10px;
				background-color: @color-background-l;
				&.active{
					width: 40px;
					background-color: @color-theme;
				}
			}
		}
		.lyric-container{
			position: absolute;
			left: 0;
			right: 0;
			top:0;
			bottom: 0;
			transform-style: preserve-3d;
			transition: all .5s linear;
			&.rotate{
				transform: rotateY(180deg);
				.albumn{
					opacity:0;
				}
				.lyric-play{
					opacity: 1;
					display: block;
				}
			}

		}

		.albumn{
			transition: all .5s linear;
			width: 600px;
			height: 600px;
			top:100px;
			left: 50%;
			position: absolute;
			margin-left:-300px;

			background: url("~common/img/disc.png") no-repeat center top;
			background-size: 600px 600px ;
			background-size: contain;
			box-sizing: border-box;
			&::after{
				content: "";
				background:url("~common/img/needle.png") no-repeat top center;
				position: absolute;
				width: 168px;
				height: 244px;
				top:-100px;
				left:52%;

				background-size:contain;

			}


			.film{
				margin:50px auto;


				width:500px;
				height: 500px;
				background: url("~common/img/disc_default.png") no-repeat;
				background-size: 100%;
				border-radius: 50%;

				.film-img{
					position:relative;
					top:50px;
					border-radius: 50%;
					display: block;
					margin: 0px auto;
					width: 400px;
					height: 400px;
					border-radius: 50%;

					
				}
				.albumn-img{
					animation:rotate 4s infinite linear;
					animation-play-state: paused;

					width: 100%;
					height: 100%;
					display: inline-block;
					border-radius: 50%;
					&.run{

						animation-play-state: running;

					}


				}
				i{
					position: absolute;
					left: 50%;
					top:50%;
					margin-top:-80px;
					margin-left:-45px;
					font-family:"iconfont" !important;

					font-style:normal;
					-webkit-font-smoothing: antialiased;
					-moz-osx-font-smoothing: grayscale;

					width:90px;
					font-size: 90px;
					color:@color-text-l;
					line-height: 160px;


					&.iconPause:before { content: "\e64a"; }
					&.iconPlay:before { content: "\e624"; }
				}


			}
		}
		.lyric-play{
			position: absolute;
			opacity: 0;
			left: 0;
			top: 0;
			bottom: 0;
			right: 0;
			display: none;
			transform:rotateY(180deg);
			transition: all .5s linear;
			overflow-y:scroll;
			.lyric-play-wrap{
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				.lyric-text{
					text-align: center;
					color:@color-text-l;
					line-height: 2;
					font-size: @font-size-medium-x;
					&.active{
						color:@color-theme;
					}
				}
				}
		

		}
		.lyric-wrap{
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			background-color: rgba(0,0,0,.5);
			transform:rotateY(180deg);
			
		}
		.wrap{
			text-align: center;
			p{
				color:rgba(255,255,255,.6);
				font-size: @font-size-medium-x;
				.no-wrap;
				line-height: 84px; 
				&.active{
					color:@color-text-green;
				}
			}
		}


	}
	.music-control{
		height: 200px;
		transform: translateY(300px);
		background: rgba(0,0,0,.3);
		animation:down .5s .5s ease-in forwards;
		color:@color-text-l;
		.colect{
			text-align: right;
			position: relative;

			.icon-xihuan{
				margin-right:30px;
				font-size: 80px;

			}
			.icon-liebiao{
				float: left;
				margin-left: 30px;
				font-size: 75px;
				&.active{
					color:@color-theme;
				}
			}
			:target{
				display: block;
			}
			.sequenceList{
				position: absolute;
				bottom:90px;
				left: 0;
				width: 100%;
				height: 700px;
				overflow-y: scroll;
				background-color: rgba(0,0,0,.4);
				box-shadow: 2px 2px 6px @color-text-l;
				display:none;
				&.showSequenceList{
					display: block;
				}


			}


			li{
				padding-left:30px;
				text-align: left;
				border-top:1px solid @color-text-l;
				color:#ddd;
				font-size: @font-size-medium;
				line-height: 90px;
				.no-wrap;
				&.active{
					color:@color-theme;
				}

			}
		}

	}
	.music-time{
		display: flex;
		flex-flow: row nowrap;
		height: 80px;
		.music-start,.music-end{
			font-size: @font-size-small;
			display: block;
			width: 110px;
			line-height: 80px;
			text-align: center;
		}
		.music-time-line{
			flex:1;
			position: relative;
			.bg-line,.music-process{
				width: 100%;
				height: 6px;
				position: absolute;
				top:39px;
				left: 0;
				background-color: #eee;

			}
			.music-process{
				width: 0;
				height: 6px;
				background-color:@color-theme;
			}

		}
	}
	.selectMode,.icon-volume{
		font-size: 80px;
		position: absolute;
		padding-left:30px;
	}
	.icon-volume{
		font-size: 60px;
		right:30px;
		
		&.active{
			color:@color-theme;
		}
	}
	.volumn{
		position: absolute;
		bottom:100px;
		right:60px;
		height: 200px;
		width: 10px;
		background-color: @color-background-l;
		.circle{
			position: absolute;
			bottom:0;
			left: 0;
			width: 10px;
			height: 100px;
			background-color: @color-theme;
			&::before,&::after{
				content:"";
				position: absolute;
				width: 34px;
				height: 34px;
				border-radius: 50%;
				background-color: @color-theme;
				top:-17px;
				left: -15px;
			}
			&::after{
				width: 18px;
				height: 18px;
				background-color: @color-background-l;
				top:-9px;
				left:-7px;
			}
		}
	}

	.download{
				// line-height: 80px;
				margin:0 auto;
				display: block;
				text-align: center;
				background-color:@color-theme;
				color:@color-text-l;
				width: 50%;
				border-radius: 50px;
				line-height: 80px;
				font-size: @font-size-large-s;
			}
		}
		.translate-enter-active,.translate-fade-active{

			transition:all .7s
		}
		.translate-enter,.translate-leave-to{
			transform: translateX(100%);
		}
		.test-lyric-height{
			line-height: 2;
					font-size: @font-size-medium-x; 
					z-index: -11;
					opacity: 0;
					position: absolute;
		}

		</style>




