<template>
  <ul class="nav">
    <li class="nav-item" v-for="nav in navs">
      <router-link class="nav-item-links" :to="nav.path" tag="a">{{nav.title}}</router-link>
    </li>
  </ul>
  </template>

<script>
export default {
  name: 'tab',
  data(){
    return{
      navs:[
        {path:"/recommend",title:"推荐"},
        {path:"/rank",title:"排行"},
        {path:"/search",title:"搜索"},
    ]

  }
  }
}
</script>

<style lang="less" type="text/less" scoped>
@import "~common/less/index";
@import "~common/less/mixin";
.nav{
  .list(@display:block);
  background-color: @color-background-l;
  padding: 0 20px;
  height: 95px;
  box-shadow: 5px 5px 5px @color-background;
  .nav-item{
    flex:1;
    text-align:center;
    .nav-item-links{
      color:@color-text;
      line-height:84px;
      font-size:@font-size-medium-x;
      display:block;
      padding-bottom: 6px;
      box-sizing: border-box;
      font-size:@font-size-medium;
      &.active{
          border-bottom:6px solid @color-theme;
      }
    }

  }
}

</style>



