 export function setIndex(ary,value,n){
 	let index=ary.indexOf(value);

 	if(n!=undefined){

 		if(index>-1){
 			if(n<ary.length){
 				ary.splice(index,1);
 				ary.splice(n,0,value);

 			}else{
 				ary.splice(index,1);
 				ary.push(value)
 			}
 		}else{
 			if(n<ary.length){
 				ary.splice(n,0,value)
 			}else{
 				ary.push(value)
 			}

 		}

 	}else{
 		if(index>-1){
 			ary.splice(index,1)
 			ary.push(value);
 		}else{
 			ary.push(value)
 		}
 		
 	}
 }
 export function math(s,e){
 	if(e){
 		return Math.round((Math.random()*s+(e-s)))
 	}
 	return Math.round((Math.random()*s))
 }


