



<template>
	<transition name="translate">

		<module :baseM="baseM" :loaded="loaded" :isAll="isAll" :list="list" @download="download">
			
			<div class="set-intro-detail" slot="title">
		
    		<h3 class="title">{{info}}</h3>
    		<p class="day">第{{day}}天</p>
    		<p class="update">{{update}} 更新</p>
    	
			</div>
			
		</module>	
	</transition>

</template>

<script>
import Module from "components/base/Module"
import {getMusicList} from "common/js/music"
import {getTop} from "api/song"
import {ERR_OK} from "api/config"
import {mapGetters,mapActions,mapMutations} from "vuex"
import {playMode} from "common/js/config"
export default{ 
	name:"set",
	components:{
		Module,
	},
	created(){
		if(!this.top){
			this.back();
			return;
		}
	
	},
	mounted(){	this._getTop();},
	watch:{
		top:function(newv,old){
			if(newv!=old){
				this._getTop();
			}
		}
	},
	methods:{
		back(){
			this.$router.back();
		},
		download(){
			if(this.on){
				this.on=false;				
				this._getTop();
			}
			
		},		
		_getTop(){
			getTop(this.top).then(res=>{

				if(res.code===ERR_OK){
				
					this.info=res.topinfo.ListName;
					this.day=res.day_of_year;
					this.update=res.date;
					this.list=[];
					
					let title="排行榜",
					imgSrc=res.topinfo.pic_album,
					total=res.topinfo.total_song_num;
					this.baseM={title,imgSrc,total}
					
					res.songlist.forEach(item=>{
						this.list.push(getMusicList(item.data))
					})
					this.loaded=true;
					this.on=true;

				}
			})
		}
		
	},
	computed:{
		
		...mapGetters([
			"top"
			])
	},
	data(){
		return{
			loaded:false,
            info:"",
            day:"",
            update:"",
			baseM:{},
			on:true,
			list:[],
			isAll:true,
		}
	}


}

</script>

<style lang="less" type="text/less" scoped>
@import "~common/less/variable";
@import "~common/less/mixin";

.set-intro-detail{

	flex:1;
	.list(column);
	height:250px;
	justify-content: center;
	.title{
		font-size: @font-size-large-s;
		line-height: 2;
	}
	.day,.update
	{
		font-size:@font-size-medium;
		line-height: 1.5;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
	}
}








</style>


