<template>
    <div class="spinner">
  <div class="rect1"></div>
  <div class="rect2"></div>
  <div class="rect3"></div>
  <div class="rect4"></div>
  <div class="rect5"></div>
</div>
</template>

<script>
	export default{ 
		name:"loading",

	}
    
</script>

<style lang="less" type="text/less" scoped>
    .spinner {
  margin: 200px auto;
  width: 100px;
  height: 120px;
  text-align: center;
  font-size: 20px;
}
 
.spinner > div {
  background-color: #67CF22;
  height: 100%;
  width: 12px;
  display: inline-block;
   
  animation: stretchdelay 1.2s infinite ease-in-out;
}
 
.spinner .rect2 {
  animation-delay: -1.1s;
}
 
.spinner .rect3 {
  animation-delay: -1.0s;
}
 
.spinner .rect4 {
  animation-delay: -0.9s;
}
 
.spinner .rect5 {
  animation-delay: -0.8s;
}
 
@keyframes stretchdelay {
  0%, 40%, 100% {
    transform: scaleY(0.4);
  }  20% {
    transform: scaleY(1.0);
  }
}
</style>


