<template>
<swiper :options="options">
  <swiper-slide class="swiper-slide" v-for="item in items" :key='item.id'>
    <!-- <div class="swiper-lazy-preloader"></div>  -->
      <div class="swiper-lazy-preloader"></div>
    <a :href="item.linkUrl" class="swiper-slide-link">
      
        <img :data-src="item.picUrl" class=" swiper-lazy"/>
    </a>
  </swiper-slide>
  <div v-if="options.pagination" class="swiper-pagination" slot="pagination"></div>
</swiper>
</template>
<script>
import {swiper,swiperSlide} from "vue-awesome-swiper"
export default {
  name: 'slide',
  components:{
    swiper,
    swiperSlide
  },
  data(){return {loadingImg:"./static/lazy.png"}},
  props:{
    options:{
      type:Object,
      default(){
        return{
          pagination:{el:".swiper-pagination"},
          autoplay:true,
          speed:300,
          loop:true,
          notNextTick:true,
          direction:"horizontal",
          autoHeight:true,
          slidesOffsetBefore:0,
          slidesOffsetAfter:0,
          slidesPerView:1,
          spaceBetween:0,
          lazy:{ 
            loadPrevNext:true

          }
        }
      }
    },
    items:{
      type:Array,
      default(){
        return[{id:"",linkUrl:"",picUrl:""}]
      }
    }
  }
}
</script>

<style lang="less" type="text/less" scoped>
.swiper-slide{
  background:url("~common/img/lazy.jpg") no-repeat top center;
}

.swiper-slide-link{
  display: block;
  min-height:256px;
  img{ 
display: block;
width:100%;
margin:0 auto;
  }

}
</style>



