import {commonParams,ops} from "api/config";
import {jsonp} from "common/js/jsonp"
import axios from "axios"
export function getSongLyric(id){



	let url="https://c.y.qq.com/lyric/fcgi-bin/fcg_query_lyric.fcg",
	data={nobase64:1,musicid:id,songtype:0,format:"json"}
	return jsonp(url,data,ops)
}

export function getSet(singermid,n){
	let url="https://c.y.qq.com/v8/fcg-bin/fcg_v8_singer_track_cp.fcg",
	p=n*15,
	data=Object.assign({},commonParams,{
		singermid:singermid,
		platform:"h5page",
		NewCode:1,
		order:"listen",
		from:"h5",
		num:15,
		begin:p,
		_:1524298192568
	});
	return jsonp(url,data,ops)
}

export function getRadio(id){
	let url="https://c.y.qq.com/v8/fcg-bin/fcg_v8_radiosonglist.fcg",
	data=Object.assign({},commonParams,{
		notice:0,
		platform:"h5",
		needNewCode:1,
		_:1524499619192,
		labelid:`${id}`,
		format:"jsonp"

	});

	return jsonp(url,data,ops)
}

export function getTop(id){
	let url="https://c.y.qq.com/v8/fcg-bin/fcg_v8_toplist_cp.fcg",
	data=Object.assign({},commonParams,{
notice:0,
platform:"h5",
needNewCode:1,
tpl:3,
page:"detail",
type:"top",
topid:`${id}`,
_:1524454795917
	});
	return jsonp(url,data,ops)

}


export function getDiss(id,p) {
  const url = '/api/diss'

  const data = Object.assign({}, commonParams, {
    notice:0,
    uin:0,
    format:"json",
platform:"h5",
needNewCode:1,
new_format:1,
pic:500,
disstid:`${id}`,
type:1,
json:1,
utf8:1,
onlysong:0,
picmid:1,
nosign:1,
song_begin:`${p*15}`,
song_num:15,
_:1524528060475,
  })
return axios.get(url,{params:data}).then(res=>Promise.resolve(res.data))
}


export function getLyric(id){
	let url="/api/lyric",
	data=Object.assign({},commonParams,{
		format:'jsonp',
		notice:0,
		nobase64:1,
		musicid:`${id}`,
		songtype:0,
		_:1524527282549,
		platform:'h5'
	});
	return axios.get(url,{params:data}).then(res=>Promise.resolve(res.data))

}
export function getVkey(id){
	let url="/api/vkey",
	data=Object.assign({},commonParams,{
loginUin:0,
hostUin:0,
notice:0,
platform:"yqq",
needNewCode:0,
cid:205361747,
format:"josnp",
songmid:`${id}`,
filename:`C400${id}.m4a`,
guid:2864283024
	});
	return axios.get(url,{params:data}).then(res=>Promise.resolve(res.data))

	
}


