<template>
  <dl>
    <dt>
      安卓：
    </dt>
    <dd>
      <ol>
        <li>打开手机菜单中的“设置”选项;</li>
        <li>选择“应用程序”;  </li>
        <li>选择“应用程序管理";"</li>
        <li>在所列的所用应用程序中选择XX音乐；</li>
        <li>点击删除按钮，然后点击确定，就能把XX音乐删除</li>
      </ol>
    </dd>
    <dt>
      iOS：
    </dt>
    <dd>
      <ol>
        <li>在手机桌面长按XX音乐的应用图标；</li>
        <li>点击图标左上角的X，即可删除XX音乐</li>
      </ol>
    </dd>
  </dl>
  </template>

<script>
export default {
  name: ''
}
</script>

<style lang="less" type="text/less" scoped>
dl{
  padding: 10px;
  position: fixed;
  z-index: 333;
  left: 0;
  top: 0;
  width:100%;
  height:100%;
  background-color: #fff;
  dt{
    font-weight:700;
    line-height: 3;
    font-size:28px;
  }
  ol{
    font-size:24px;

    line-height:1.5;
    li{
      list-style-type:decimal;
    }
  }
}

</style>



