<template>
	<div class="mini-player">
		<div class="play_bar" @click="turn">
			<svg v-show="!playing" class="play_icon" width="200px" height="200.00px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"><path fill="#1296db" d="M512 0C230.4 0 0 230.4 0 512s230.4 512 512 512 512-230.4 512-512S793.6 0 512 0z m0 981.333333C253.866667 981.333333 42.666667 770.133333 42.666667 512S253.866667 42.666667 512 42.666667s469.333333 211.2 469.333333 469.333333-211.2 469.333333-469.333333 469.333333zM672 441.6l-170.666667-113.066667c-57.6-38.4-106.666667-12.8-106.666666 57.6v256c0 70.4 46.933333 96 106.666666 57.6l170.666667-113.066666c57.6-42.666667 57.6-106.666667 0-145.066667z" /></svg>
			<svg v-show="playing" class="pause_icon" width="200px" height="200.00px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"><path fill="#1296db" d="M512 40.96a471.04 471.04 0 1 0 471.04 471.04A471.04 471.04 0 0 0 512 40.96z m0 901.12a430.08 430.08 0 1 1 430.08-430.08 430.08 430.08 0 0 1-430.08 430.08zM593.92 307.2h40.96v409.6h-40.96zM389.12 389.12h40.96v327.68h-40.96zM389.12 307.2h40.96v40.96h-40.96z" /></svg>
		</div>
		<div class="music-message">


			<h5 class="songname" v-html="currentSong.songname"></h5>
			<div class="lyric-wrap">
				
			</div>


		</div>
		<div class="download"><i class="icon-download"></i></div>

	</div>
	
</template>

<script>
import {mapGetters,mapMutations} from "vuex";

import {playMode} from "common/js/config"
export default{ 
	name:"miniPlayer",
	mounted(){
		this.addEventListener();
	},
	beforeDestroyed(){
		this.removeEventListener();
		document.querySelector("audio").pause();
	},
	methods:{
		turn(){
			if(this.playing){
				this.setPlaying(false);
				document.querySelector("audio").pause();
			}else{
				this.setPlaying(true);
				document.querySelector("audio").play();
			}
			

		},
		removeEventListener(){
			let el=document.querySelector("audio");
			el.removeEventListener("timeupdate",this.upData);
			el.removeEventListener("canplay",this.getDuration);
		},
		addEventListener(){
			let el=document.querySelector("audio");
			el.addEventListener("timeupdate",this.upData);
			el.addEventListener("canplay",this.getDuration);
		},
		getDuration(){
			this.duration=this.getTime(document.querySelector("audio").duration)
		},
		getTime(t){
			let m=Math.floor(t/60),
			s=Math.floor(t%60);
			m=m<10?("0"+m):m;
			s=s<10?("0"+s):s;
			return m+":"+s;
		},
		upData(){
			if(document.querySelector("audio")){
				this.currentTime=this.getTime(document.querySelector("audio").currentTime);
			}
		},
		nextPlay(){
			let n=this.currentIndex;
			if(this.mode===playMode.sequence){
				n=(n+1)%this.sequenceList.length;
				this.setCurrentIndex(n)
				return;
			}else if(this.mode===playMode.random){
				let n2=math(this.sequenceList.length);
				while(n2==n){
					n2=math(this.sequenceList);
				}
				this.setCurrentIndex(n2);

			}
			document.querySelector("audio").currentTime=0;
			document.querySelector("audio").play();
		},
		...mapMutations({
			"setPlaying":"SET_PLAYING"
		})
	},
	computed:{

		currentSong(){
			return this.sequenceList[this.currentIndex];
		},

		...mapGetters([
			"sequenceList",
			"currentIndex",
			"mode",
			"playing"
			])
		
	},
	data(){
		return{
			currentTime:"",
		}
	}

}

</script>

<style lang="less" type="text/less" scoped>
@import "~common/less/variable";
@import "~common/less/mixin";
.mini-player{
	padding-top:80px;
	height: 92px;
	.list(row nowrap);
	// position: absolute;
	// bottom: 0;
	// left: 0;
	// right: 0;
	justify-content: center;
	align-items: center;
	align-content: center;
	.play_bar{
		width: 92px;
		height: 92px;
		.play_icon,.pause_icon{
			position: absolute;

			display: block;
			width: 92px;
			height: 92px;
			color:@color-theme;
		}
	}
	.music-message{
		flex:1;
	}
	.download{
		width: 92px;
		.icon-download{
			font-size: 80px;
			line-height: 92px;
			color:@color-theme;
		}
	}

}

.co{
	position: absolute;
	top: 0;
	left: 0;
	z-index: 2;
	width: 92px;
	height: 92px;

}



</style>


