<template>
	<div id="app" cloak>
		<div  id="app-contain-wrap">
			<m-head class="app-top"></m-head>
			
			<keep-alive>
				<router-view v-if="$route.meta.keepAlive"></router-view>
			</keep-alive>
			<router-view v-if="!$route.meta.keepAlive"></router-view>

			
		</div>


		<player v-if="fullScreen"></player>
		<audio autoplay :src="audioSrc"></audio>




		<!-- <player></player> -->

		
	</div>
</template>
<script>
import MHead from "components/m-head"
import Player from "components/player"
import {getVkey} from "api/song"
import {mapGetters,mapMutations} from "vuex"
import {playMode} from "common/js/config"
export default {
	name: 'App',
	components:{
		MHead,
		Player
	},
	mounted(){this.addEventListener()},
	beforeDestroyed(){this.removeEventListener()},
	methods:{
		addEventListener(){
			let el=document.querySelector("audio");
			el.addEventListener("ended",this.nextPlay);
			el.addEventListener("error",this.error)
		},
		removeEventListener(){
			let el=document.querySelector("audio");
			el.removeEventListener("ended",this.nextPlay);
		},
		nextPlay(){
			let n=this.currentIndex;
			if(this.mode===playMode.sequence){
				n=(n+1)%this.sequenceList.length;
				this.setCurrentIndex(n);
				
				return;
			}else if(this.mode===playMode.random){
				let n2=math(this.sequenceList.length);
				while(n2==n){
					n2=math(this.sequenceList);
				}
				this.setCurrentIndex(n2);
				return;

			}

			document.querySelector("audio").currentTime=0;
			document.querySelector("audio").play();
		},
		...mapMutations({
			"setCurrentIndex":"SET_CURRENTINDEX",
			"setVkey":"SET_VKEY"
		}),
		error(){
			if(this.currentSong){
		getVkey(this.currentSong.songmid).then(res=>this.setVkey(res.data.items[0].vkey))

			}

	}},
	computed:{
		audioSrc(){
			if(this.currentSong){

				return `http://dl.stream.qqmusic.qq.com/C400${this.currentSong.songmid}.m4a?guid=2864283024&vkey=${this.vkey}&uin=0&fromtag=38`
			}
			return"";
			
			
		},
		currentSong(){
			return this.sequenceList[this.currentIndex];
		},
		...mapGetters([
			"fullScreen",
			"sequenceList",
			"currentIndex",
			"mode",
			"vkey"
			])
	},
	data(){
		return{
				
		}
	}
}

</script>
<style lang="less" type="text/less">
@import "~common/less/index";
@import "~common/less/mixin";

#app {

	// background-color: @color-background;
	
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;

	max-width: 640px;
	.list(column);
	.app-top{
		// height: 185px;
	}
}
	// // position: relative;
	// }
	#app-contain-wrap{
		position: absolute;
		left: 0;
		top: 0;
		bottom: 0;
		right: 0;
		overflow-y: scroll;
	}


	</style>



