<template>
  <section class="page5">
<div class="limit-width">
  
    <h3 class="logo">酷狗音乐<img  :src="logoSrc" /></h3>
    
    <div class="button-set">
      <img v-for="img in buttonSet" :src="img.src" @click="_event(img.events)" :class="img.class"/>
    </div>
    <p class="notice">
      我们致力于推动网络正版音乐的发展
    </p>
   
    <p class="links">
      <router-link to="/recommend">在线听歌</router-link>
      <span class="line">|</span>
      <router-link to="/welcome/server">用户服务协议</router-link>
      <span class="line">|</span>
      <router-link to="/welcome/uninstall">卸载说明</router-link>
    </p>
    <footer class="footer">
      <p>
        粤ICP备09017694号-2
      </p>
      <p>
        公司名称：广州XX计算机科技有限公司
      </p>
      <p>
        公司地址：广州市天河区黄埔大道中315号羊城晚报创意产业园内自编号1-18
      </p>
      <p>
        公司电话：020-37529000
      </p>
    </footer>
</div>
  </section>
  </template>

<script>
export default {
  name: 'EndPage',
  methods:{
    _event:function(cname){
      if(cname==="comeHome"){

          this.$emit("come")
      }else if(cname=="share"){

      }

    }
  },
  props:["come"],
  data(){
    return{
      logoSrc:"./static/logo.png",
      buttonSet:[
        {
          class:"come-top",
          src:"./static/tt.png",
          events:"comeHome"
        },
       
      ]
    }

}
}
</script>

<style lang="less" type="text/less" scoped>
  @import "~common/less/index";
  .page5{
    padding-top:120px;
    line-height:1.5;
    font-size:@font-size-small;
    color:@color-text-d;
    text-align: center;
    .limit-width{
  width:100%; max-width: 640px; left:0; bottom:0; top:50px;
      margin: 0 auto; 
    }

    .logo{
      font-size:0;
      img{
        width:26%;
        border-radius: 50%;
        display:inline-block;
      }
      margin-bottom:24px;

    }
    .button-set{
      margin:90px 0 ;
      img{
        display:inline-block;
        width:37.7%;
        margin:0 3.33%;
      }
    }
    .notice{
      font-size:@font-size-small;

    }
    .links{
      color:@color-theme;
      font-size:@font-size-medium-x;
      a{
        color:@color-theme;
      }
      margin-top:80px;
      .line{
        margin:0 @font-size-small;
      }
    }
    .footer{
      margin-top:30px;
      color: @color-text-g;
      font-size:@font-size-small;
    }
  }
</style>



