import Vue from 'vue'
import Router from 'vue-router'
import Welcome from 'components/welcome'

import Rank from 'components/rank'
import Recommend from 'components/recommend'
import Search from 'components/search'
import List from 'components/list'
import Singer from 'components/singer'

import ServerAgrement from 'components/tool/ServerAgrement'
import Uninstall from 'components/tool/Uninstall'

import fastclick from 'fastclick'
import VueLazyload from 'vue-lazyload'
import SongSet from 'components/song-set'
import Top from "components/top"
import Diss from "components/diss"



fastclick.attach(document.body)

Vue.use(Router)
Vue.use(VueLazyload,{loading:require('common/img/lazy2.png'),preLoad:1})




export default new Router({
  linkActiveClass: 'active',
  routes: [{
    path: '/',
    redirect: '/welcome'
  },
  {
    path: '/recommend',
    component:Recommend,
    meta:{keepAlive:true}

  },
  {
    path: '/welcome',
    component: Welcome,
    children:[
      {
        path: 'server',
        component: ServerAgrement
      },
      {
        path: 'uninstall',
        component: Uninstall
      },

    ],
    meta:{keepAlive:true}

  },
  {
    path: '/rank',
    component: Rank,
    meta:{keepAlive:true}
  },
   {
    path: '/search',
    component: Search,
    meta:{keepAlive:true}
  },
  {
    path:"/set/:id",
    component:SongSet,
    meta:{keepAlive:false}
  },
  {
    path:"/top/:id",
    component:Top,
    meta:{keepAlive:false}
  },
  {
    path:"/diss/:id",
    component:Diss,
    meta:{keepAlive:false}
  }

  ]
})



