<template>
	<div class="set" ref="set">
		<i class="icon-fanhui" @click="back"></i>
		<loading v-if="!loaded"></loading>
		<div class="wrap" v-else>

			<div class="set-title" ref="setTitle">
				<div class="seat" ref="seat"></div>

				<div class="set-title-wrap">
					<img :src="baseM.imgSrc" class="set-title-bg">
					<div class="set-intro">
						<div class="set-intro-img">
							<img :src="baseM.imgSrc" >
						</div>
						<slot name="title"></slot>

					</div>
					<div class="set-play-all">
						<div class="run-play-all-btn" v-if="!isStart">
							<span @click="selectAll">播放全部</span>
						</div>
						<mini-player v-else></mini-player>
					</div>
				</div>
			</div>
			<div class="panel" ref="panel" @scroll="scrollEvent">
				<div slot="list" class="set-music-list">
					<h4>{{baseM.title}} 共{{baseM.total}}首</h4>
					<ul>
						<li class="list-item" v-for="(item,index) in list" @click="playOne(item)">
							<span>{{index+1}}</span>
							<h5>{{item.songname}}</h5>
							<p>{{item.singer}}</p>
						</li>
						<li v-if="!isAll" class="list-item load-more" @click="downloadMore">点击加载更多数</li>
						<li v-else class="list-item loaded-all">已经全部加载</li>
					</ul>
				</div>
				<slot class="set-music-list" name="list-other">

				</slot>
			</div>
		</div>

	</div>

</template>

<script>
import Loading from "components/base/Loading"
import {getMusicList} from "common/js/music"
import {getSet} from "api/song"
import {ERR_OK} from "api/config"
import {mapGetters,mapMutations,mapActions} from "vuex"
import MiniPlayer from "components/MiniPlayer"
import {playMode} from "common/js/config"
export default{ 
	name:"set",
	components:{
		Loading,
		MiniPlayer
	},
	data(){
		return{
			isStart:false,
		}
		
	
	},
	props:{
		baseM:{
			type:Object,
			default:{
				imgSrc:"",
				title:"",
				total:"",
			}
		},
		loaded:{
			type:Boolean,
			default:false
		},
		isAll:{
			type:Boolean,
			default:false
		},
		list:{
			type:Array,
			default(){
				return []
			}
		}
	},
	update(){
		this.scrollEvent();
	},
	computed:{
		Oheight(){
			return this.$refs.seat.offsetHeight;
		},
		audio(){
			return document.querySelector("audio");
		},
		height(){
			if(this.$refs.panel){
				return this.$refs.panel.scrollTop
			}else{
				return"";
			}
			
		},
		...mapGetters([
			"playing",
			"sequence"
			])
	},
	beforeDestroyed(){
		this.audio.pause();
		this.setFullScreen(false);
		this.setPlaying(false);
		this.isStart=false;
		this.$refs.setTitle.style.height=this.Oheight+"px";
	},
	methods:{
		downloadMore(){
			this.$emit("download");
		},
		playOne(item){
			this.selectPlay({list:[item],index:0,fullScreen:false})
			this.setMode(playMode.loop);
			this.isStart=true;

		},
		selectAll(){
			this.selectPlay({list:this.list,index:0,fullScreen:false})
			this.setMode(playMode.sequence);
			this.isStart=true;
			this.audio.play()
		},
		back(){
			this.setFullScreen(false);
			this.audio.pause();
			this.isStart=false;
			this.setPlaying(false);
			this.$router.back();
			this.$refs.setTitle.style.height=this.Oheight+"px";
		},
		scrollEvent(){
			let setTile=this.$refs.setTitle,
			panel=this.$refs.panel;
			let change=this.Oheight-panel.scrollTop;
			if(change>=150){
				setTile.style.height=`${change}px`;
				
			}else if(panel.scrollTop>300){
				setTile.style.height="170px";

			}else{
				setTitle.style.height=this.Oheight+"px";
				setTile.style.left
			}
		},
		...mapMutations({
			"setFullScreen":"SET_FULLSCREEN",
			"setPlaying":"SET_PLAYING",
			"setMode":"SET_MODE"
		}),
		...mapActions([
			"selectPlay"
			])

		
	}
}

</script>

<style lang="less" type="text/less" scoped>
@import "~common/less/variable";
@import "~common/less/mixin";
.set{
	position:fixed;
	top: 0;
	left: 0;
	right:0;
	bottom:0;
	max-width: 640px;
	z-index: 1001;
	// overflow:hidden;
	color:@color-text-l;
	background-color: @color-background-l;
	.set-title{
		background-color:rgba(0,0,0,.7);
		position: absolute;
		left: 0;
		top: 0;
		height:550px;
		width: 100%;
		z-index: 999;
		
		.set-title-wrap{
			height: 550px;
			width: 100%;
			position: absolute;
			bottom: 0;
			left: 0;
			padding:120px 32px 0;
			box-sizing: border-box;
		

		}
			.seat{
				width: 100%;
				height: 100%;
				position: absolute;
				z-index: -1;

			}
		.set-title-bg{
			position: absolute;
			left:0;
			top:0;
			width: 100%;
			height: 100%;
			filter:blur(20px);
			z-index: -1;
		}
		.set-intro{

			.list(row,display);
			.set-intro-img{
				width: 250px;
				height: 250px;
				margin-right:20px;
				img{
					width: 100%;
				}

			}
			.set-intro-detail{

				flex:1;
				.list(column);
				height:250px;
				justify-content: center;
				.set-singer{
					font-size: @font-size-large-s;
					line-height: 2;
				}
				
			}


		}
		.set-play-all{
			height: 168px;
			text-align: center;
			.list(column);
			
			span{
				margin-top:70px;
				display:inline-block;
				line-height: 80px;
				width: 340px;
				text-align: center;
				background-color: @color-theme;
				color:@color-text-l;
				border-radius: 60px;

				&::before{
					content:"";
					height: 0;
					width:5px;
					display: inline-block;
					line-height: 80px;
					border:15px solid transparent;
					border-left-color:@color-text-l;
				}


			}
			
		}

	}
	
	

	.panel{

		color:@color-text-d;
		position: absolute;
		left: 0;
		top: 0;
		bottom:0;
		right: 0; 
		box-sizing: border-box;
		padding-top:550px;
		;
		overflow-y: scroll;
	}
}
.icon-fanhui{
	font-size: 60px;
	position: absolute;
	z-index: 1000;
	left: 20px;
	top: 10px;

}
.set-music-list{

	color:@color-text;
	h4{
		padding:0 28px;
		font-size: @font-size-medium;
		line-height: 108px;
		color:#777;
	}
	.list-item{
		padding-left: 90px;
		position: relative;
		height: 124px; 
		.list(column);
		justify-content: center;
		line-height: 1.4;
		.no-wrap;
		span{
			position: absolute;
			color:@color-danger;
			left: 0;
			top: 0;
			line-height: 108px;
			width: 90px;
			text-align:center;
		};
		h5{
			color:rgba(0,0,0,.8);
			font-size: @font-size-large;
			.no-wrap;
		}
		p{
			font-size: @font-size-medium;
			.no-wrap;
			color:rgba(0,0,0,.6)
		}
		&.load-more,&.loaded-all{
			padding-left:0;
			text-align: center;
			font-size: @font-size-medium-x;
			color:@color-text-d;

		}
	}
}



</style>


