 import * as types from './mutation-types'
 import {playMode} from 'common/js/config'

 function findIndex(list,song){
 	return list.findIndex(item=>{
 		return item.id===song.id
 	})
 }
 export const selectPlay=function ({commit,state},{list,index,fullScreen}){
 	commit(types.SET_SEQUENCELIST,list)
 	if(state.mode===playMode.random){
 		let randomList=list.sort(()=>Math.random()-0.5);
 		commit(types.SET_LIST,randomList)
 		index=findIndex(randomList,list[index])
 	}else{
 		commit(types.SET_LIST,list)
 	}
 	commit(types.SET_CURRENTINDEX,index)
 	commit(types.SET_PLAYING,true)

 	commit(types.SET_FULLSCREEN,fullScreen)
 	
 	

 }
export const randomPlay=function({commit},{list}){
	commit(types.SET_MODE,playMode.random);
	commit(types.SET_SEQUENCELIST,list);
	commit(types.SET_LIST,list.sort(()=>Math.random()-0.5));
	commit(types.SET_FULLSCREEN,true);
	commit(types.SET_PLAYING,true)
}




