<template>
	<div ref="wrap" class="wrap">
		<slot ></slot>

	</div>
</template>

<script>
export default{ 
	name:"scrolll",
	computed:{
		maxScrollTop(){
			return this.pH-this.wH
		}
	},
	props:{
		on:{
			type:Boolean,
			default:true
		}
	},
	data(){
		return{
			wH:0,
			pH:0,
			touchStart:0,
			change:0,
			curScroll:0,
			updata:true,
			time:""

		}
	},
	mounted(){
		this._inits();
	},
	methods:{
		_inits(){this.wH=window.innerHeight;
			
			let el=this.$refs.wrap,
			body=document.documentElement||document.body;

			
			el.addEventListener("touchstart",(e)=>{
				e.stopPropagation()
				let wrap=document.getElementById("app-contain-wrap");
				this.pH=wrap.scrollHeight;
				this.wH=window.innerHeight;
				this.scrollTop=wrap.scrollTop;
				let move=(e)=>{
					e.stopPropagation();
					if(this.on){
						let change=e.touches[0].clientY-this.touchStart;
						if((this.curScroll<=0&&change>0)||(this.curScroll>=this.maxScrollTop&&change<0)){
							if(this.curScroll<=0&&change>0){
								change=Math.min(change,200);
								el.style.transform=`translateY(${change}px)`;


							}else if(this.curScroll>=this.maxScrollTop&&change<0){
								change=Math.max(change,-200);
								el.style.transform=`translateY(${change}px)`;

							}
							this.change=change;
							

						}}

					};
					let endT=()=>{
						e.stopPropagation();
						if(this.on){
							if(this.curScroll<=0||this.curScroll>=this.maxScrollTop){
								el.style.transition="all .2s linear";

								el.style.transform="translateY(0)";

							}
							if(this.curScroll>0&&this.curScroll>=this.maxScrollTop){
								this.endEvent();

							}

							this.change=0;
							el.removeEventListener("touchmove",move);
							el.removeEventListener("touchend",endT)

						}
					};
					if(this.on&&((body.scrollTop<=0)||(body.scrollTop>=this.maxScrollTop))){

						el.style.transition='';
						this.touchStart=e.touches[0].clientY;
						this.curScroll=wrap.scrollTop;
						el.addEventListener("touchmove",move);
						el.addEventListener("touchend",endT)
					}else{
						el.removeEventListener("touchmove",move);
						el.removeEventListener("touchend",endT)
					}
					
					

					
				}
				)
		},
		endEvent(){
			this.$emit("end")
		}
	}
}
</script>

<style lang="less" type="text/less" scoped>
</style>


