<template>
    <ul class="history">
        <li class="history-item" v-for="(item,index) in history" >
            <i class="icon-lishijiluxianxing"></i>
            <span @click="search(item)">{{item}}</span>
            <i class="icon-shachu-xue" @click="deleteHistory(index)"></i>
        </li>
    </ul>
</template>

<script>
	export default{ 
		name:"history",
        methods:{
deleteHistory(index){this.$emit("delete",index)},
search(val){
    this.$emit("copy",val)
}
        },
		props:{
            history:{
                type:Array,
                default(){
                    return[]
                }
            },

			}
		}


    
</script>

<style lang="less" type="text/less" scoped>
@import '~common/less/variable';
.history{
    background-color: @color-background-l;
    .history-item{
        border-top:2px solid @border-color-g;
        padding:0 30px;
        position: relative;
       margin:0 30px;position: relative;
       height:90px;
        .icon-lishijiluxianxing{
            position: absolute;
            left: 0;
            top: 24px;
            font-size: @font-size-large-s;
        }
        .icon-shachu-xue{
            position: absolute;
            right: 0;
            top: 24px;
            font-size: @font-size-large-s;
        }
        span{
            position: absolute;
    left: 80px;
    right: 100px;
    height: 88px;
    line-height: 88px;
    color: @color-text;
    font-size: @font-size-medium;
    font-size: @font-size-medium;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
        }
    }
}
    
</style>


