<template>
	<transition name="translate">

		<module :baseM="baseM" :loaded="loaded" :isAll="isAll" :list="list" @download="download">
			
			<div class="set-intro-detail" slot="title">
				<div>
					<h3 class="set-singer">{{singer}}</h3>
				<p class="set-fans"><span>粉丝:</span>{{fans}}</p>
				<p class="set-singer-intro
				">{{singerDesc}}</p>
				</div>
				
			</div>
			
		</module>	
	</transition>

</template>

<script>
import Module from "components/base/Module"
import {getMusicList} from "common/js/music"
import {getSet} from "api/song"
import {ERR_OK} from "api/config"
import {mapGetters,mapActions,mapMutations} from "vuex"
import {playMode} from "common/js/config"
export default{ 
	name:"set",
	components:{
		Module,
	},
	created(){
		if(!this.set){
			this.back();
			return;
		}
		this._getSet();
	},
	watch:{
		set:function(newv,old){
			if(newv!=old){
				this._getSet();
			}
		}
	},
	methods:{
		back(){
			this.$router.back();
		},
		download(){
			if(this.on){
				this.on=false;				
				this._getSet();
			}
			
		},		
		_getSet(){
			console.log("准备拉数据")

			getSet(this.set,this.p).then(res=>{

				if(res.code===ERR_OK){
					let data=res.data;
					this.singerDesc=data.SingerDesc;
					this.singer=data.singer_name;
					this.fans=Math.floor(data.fans/1000)/10+"万人";
					this.total=total;

					let total=data.total,
					imgSrc=`https://y.gtimg.cn/music/photo_new/T001R150x150M000${data.singer_mid}.jpg?max_age=2592000`,
					title="歌曲";
					this.baseM={imgSrc,total,title};
					data.list.forEach(item=>{
						this.list.push(getMusicList(item.musicData))
					})
					this.loaded=true;
					this.p++;
					this.on=true;

				}
			})
		},
		...mapMutations({
"setMode":"SET_MODE"
		}),
		...mapActions([
			"selectPlay"
			])
		
	},
	computed:{
		isAll(){
			return this.list.length>=this.total;
		},
		
		...mapGetters([
			"set"
			])
	},
	data(){
		return{
			loaded:false,
			p:0,
			singerDesc:"",
			singer:"",
			fans:"",
			baseM:{},
			on:true,
			list:[]
		}
	}


}

</script>

<style lang="less" type="text/less" scoped>
@import "~common/less/variable";
@import "~common/less/mixin";

.set-intro-detail{

	flex:1;
	.list(column);
	height:250px;
	justify-content: center;
	.set-singer{
		font-size: @font-size-large-s;
		line-height: 2;
	}
	.set-fans{
		font-size:@font-size-medium;
		line-height: 1.5;
	}
	.set-singer-intro
	{
		height: 80px;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		font-size:@font-size-medium;
					line-height: 1.5;
					display: -webkit-box;
					-webkit-box-orient: vertical;
					-webkit-line-clamp: 2;
					overflow: hidden;
	}
}








</style>


