 export function formatLyric(lyric){
let ary=[];
 	lyric=lyric.replace(/&#(\d+);/g,(...arg)=>{
 		let [big,small]=arg,
 		val=big;
 		switch(small){
 			case  32:
 			val=" ";
 			break;
 			case 45:
 			val="-";
 			break;
 		}
 		return val;
 	})
 	lyric.replace(/\[(\d{2})&#58;(\d{2})&#46;\d{2}\]([^$&%#;]+)(&#10)?/g,(...arg)=>{
 		let obj=new Object;
 		let [,minite,second,text]=arg;
 		ary.push({minite,second,text})
 	})
 	return ary;
 }


